import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { backendUrl } from '../../config/config';
import logo from '../../assets/components/Wrapper/Header/logo.webp';

import './index.scss';
import { Helmet } from 'react-helmet';
import HeadingBlock from '../../components/HeadingBlock';
import dayjs from 'dayjs';
import {Link} from "react-router-dom";
import Spinner from "../../components/Spinner";
import ErrorIndicator from "../../components/Errors/ErrorIndicator";

const BlogAll = React.memo((props) => {

    const [ loading, setloading ] = useState(false);
    const [ error, setError ] = useState(false);
    const [ posts, setPosts ] = useState(null);

    useEffect(()=> {
        setloading(true);
        axios.get(backendUrl + /posts/)
            .then(res => {
                setPosts(res.data);
                if (res.data[0]) {
                    props.setBreadPosts(res.data);
                }
                setError(false);
                setloading(false);

            })
            .catch(err => {
                setError(true);
                setloading(false);
            });
    }, []);

    const createJSONLD = () => {
        if (posts) {
            const date = {
                '@context': 'https://schema.org/',
                "@type": "CreativeWork",
                "author": {
                    "@type": 'Organization',
                    'name': '24ROLLS',
                },
                'contentLocation': 'Днепр, Запорожье',
                'about': posts.map(el => el.title).join(', '),
                'text': posts.map(el => el.content).join(', '),
                'dateCreated': dayjs(posts[0].date).format('DD.MM.YYYY'),
                'datePublished': dayjs(posts[0].date).format('DD.MM.YYYY'),
                'keywords': 'Суши, роллы, Харьков, Днепр, Днепропетровск, Запорожье, вкусно, быстрая доставка',
                'material': 'Блог'
            };
    
            return JSON.stringify(date);
        }
    }

    return(
        <div className={'blog-all'}>
            {loading && <Spinner /> }
            {error && <ErrorIndicator />}
            {!loading && !error && posts &&
            <>
                <HeadingBlock
                    title={'Блог'}
                    h={'h1'}
                    svg={true}
                />

            <Helmet>
                <title>
                    {props.seo ? props.seo.title : '24ROLLS'}
                </title>
                <meta name="description" content={props.seo && props.seo.description} />
                <meta name="keywords" content={props.seo && props.seo.keywords} />
                <script async type="application/ld+json">
                    {createJSONLD()}
                </script>
                <script type="application/ld+json">
                    {JSON.stringify({ "@context": "http://schema.org", "@type": "WebSite", "@id": "#website", "url": "https://24rolls.com.ua/", "name": "\u0421\u0443\u0448\u0438 24Rolls Dnepr", "potentialAction": { "@type": "SearchAction", "target": "https://24rolls.com.ua/?s={search_term_string}", "query-input": "required name=search_term_string" } })}
                </script>
                <script type="application/ld+json">
                    {JSON.stringify({ "@context": "http://schema.org", "@type": "Organization", "url": "https://24rolls.com.ua/", "logo": logo, "contactPoint": [{ "@type": "ContactPoint", "telephone": "068 719 24 24", "contactType": ["customer service", "sales"], "areaServed": ["UA"], "contactOption": ["TollFree"], "availableLanguage": ["Russian", "Ukrainian"] }], "sameAs": ["https://www.youtube.com/channel/UCmJwKEidDraJtVdYtpnsJSA", "https://www.instagram.com/24rolls.com.ua/"] })}
                </script>
            </Helmet>

                <div className="blog-all_content">
                    {posts[0] && 
                        posts.map(post =>
                            <Link
                                to={props.match.path.slice(0, 3) + `/posts/${post.route}`}
                                className={'list'}
                                key={post._id}
                            >
                                <div className={'list_context'}>
                                    <img
                                        src={backendUrl + `/${post.image}`}
                                        alt={post.alt}
                                    >
                                    </img>
                                    <div className={'footer'}>
                                        <h3>
                                            {post.title}
                                        </h3>
                                        <p>
                                            {dayjs(post.date).format('DD.MM.YYYY')}
                                        </p>
                                        <li>
                                            <span style={{fontSize: '14px'}}>
                                                {'Посмотреть больше...'}
                                            </span>
                                        </li>
                                    </div>
                                </div>
                            </Link>
                        )
                    }
                    {!posts[0] && <p>{'Пока что здесь ничего нет...'}</p>}
                </div>
            </>
            }
        </div>
    )
});

export default BlogAll;